#notfound {
  box-sizing: border-box;
  position: relative;
  height: 100vh;
  background-color: #fafbfd;
}

.notfound {
  box-sizing: border-box;
  max-width: 520px;
  width: 100%;
  text-align: center;
  display: grid;
  place-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.notfound-bg {
  text-align: center;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: -1;
}

.notfound-bg > div:nth-child(1) {
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  background: #fff;
  border-radius: 90px;
  height: 125px;
  box-shadow: 5px 5px 0 0 #f3f3f3;
}
.notfound-bg > div:nth-child(2) {
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  background: #fff;
  border-radius: 90px;
  height: 125px;
  transform: scale(1.3);
  box-shadow: 5px 5px 0 0 #f3f3f3;
  position: relative;
  z-index: 10;
}

.notfound-bg > div:nth-child(3) {
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  background: #fff;
  border-radius: 90px;
  height: 125px;
  box-shadow: 5px 5px 0 0 #f3f3f3;
  position: relative;
  z-index: 90;
}

.notfound h1 {
  text-align: center;
  box-sizing: border-box;
  font-family: quicksand, sans-serif;
  font-size: 86px;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 8px;
  color: #151515;
}

.notfound h2 {
  text-align: center;
  box-sizing: border-box;
  font-family: quicksand, sans-serif;
  font-size: 26px;
  margin: 0;
  font-weight: 700;
  color: #151515;
}

.notfound a {
  text-align: center;
  box-sizing: border-box;
  font-family: quicksand, sans-serif;
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
  background: #18e06f;
  display: inline-block;
  padding: 15px 30px;
  border-radius: 5px;
  color: #fff;
  font-weight: 700;
  margin-top: 20px;
  transition: all 0.5s ease;
}

.notfound a:hover {
  background: #47ff97;
}
